"use client";

import { useState } from "react";

export default function AddProductOverlay() {
  const [showOverlay, setShowOverlay] = useState(false);
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [pdfFile, setPdfFile] = useState<File | null>(null);

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file && file.type.startsWith("image/")) {
      setImageFile(file);
    } else {
      alert("Please select a valid image file.");
    }
  };

  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file && file.type === "application/pdf") {
      setPdfFile(file);
    } else {
      alert("Only PDF documents are allowed.");
    }
  };

  return (
    <div className="relative min-h-screen">
      {/* Bottom-right button */}
      <button
        onClick={() => setShowOverlay(true)}
        className="fixed bottom-4 right-4 bg-blue-600 text-white px-4 py-2 rounded shadow-lg"
      >
        Add Product
      </button>

      {/* Overlay */}
      {showOverlay && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-white p-8 rounded shadow-xl max-w-md w-full relative">
            <button
              onClick={() => setShowOverlay(false)}
              className="absolute top-2 right-2 text-gray-500 hover:text-gray-700"
            >
              âœ•
            </button>
            <h2 className="text-xl font-bold mb-6">Upload Product Files</h2>

            {/* Image Upload */}
            <div className="mb-4">
              <label className="block mb-1 font-medium">Product Image</label>
              <input
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                className="w-full"
              />
              {imageFile && (
                <img
                  src={URL.createObjectURL(imageFile)}
                  alt="Preview"
                  className="mt-2 w-32 h-32 object-cover rounded border"
                />
              )}
            </div>

            {/* PDF Upload */}
            <div className="mb-4">
              <label className="block mb-1 font-medium">
                Product Document (PDF)
              </label>
              <input
                type="file"
                accept="application/pdf"
                onChange={handlePdfChange}
                className="w-full"
              />
              {pdfFile && (
                <p className="mt-2 text-sm text-gray-700">
                  Selected: {pdfFile.name}
                </p>
              )}
            </div>

            {/* Submit (optional) */}
            <button
              className="mt-4 bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
              onClick={() => {
                if (!imageFile || !pdfFile) {
                  alert("Please upload both image and PDF.");
                  return;
                }
                // handle form submission here
                alert("Files ready to upload!");
                setShowOverlay(false);
              }}
            >
              Submit
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
